/* 로그인 페이지 전체를 중앙에 배치 */
.login-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
}

/* 로그인 박스 스타일 */
.login-box {
    background-color: white;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    padding: 40px;
    text-align: center;
    width: 300px;
}

/* 로그인 헤더 스타일 */
.login-header {
    margin-bottom: 20px;
    font-size: 24px;
    font-weight: bold;
}

/* 버튼 기본 스타일 */
.login-button {
    width: 100%;
    padding: 10px;
    margin-bottom: 10px;
    font-size: 16px;
    border-radius: 5px;
    border: none;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    font-weight: bold;
}

/* 각 로그인 버튼의 색상 */
.kakao {
    background-color: #fee500;
    color: #3c1e1e;
}

.naver {
    background-color: #03c75a;
    color: white;
}

.google {
    background-color: #db4437;
    color: white;
}

/* 로그인 아이콘 스타일 */
.login-icon {
    margin-right: 10px;
    font-size: 20px;
}

/* 버튼 호버 스타일 */
.login-button:hover {
    opacity: 0.9;
}
